import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
// context
import { useContext, useState } from "react";
import { todoContext } from "../context/context";

export default function EditModal({ data, open, setOpen }) {
  const { todos, setTodos } = useContext(todoContext);
  const [newTodoValue, setNewTodoValue] = useState({
    title: data.title,
    des: data.des,
  });

  const handleEdit = () => {
    const newTodo = todos.map((e) => {
      if (e.id === data.id) {
        e.title = newTodoValue.title;
        e.des = newTodoValue.des;
      }
      return e;
    });
    // localStorage.setItem("REACT-Todos", JSON.stringify(newTodo));
    setTodos(newTodo);
    handleClose();
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <Dialog open={open} onClose={handleClose}>
      <DialogTitle>تعديل المهمة</DialogTitle>
      <DialogContent>
        <TextField
          sx={{ marginBottom: "20px" }}
          autoFocus
          margin="dense"
          id="name"
          name="مهمة"
          label="تعديل المهمة"
          type="text"
          fullWidth
          value={newTodoValue.title}
          onChange={(e) =>
            setNewTodoValue({ ...newTodoValue, title: e.target.value })
          }
        />
        <TextField
          label="تفاصيل المهمة"
          fullWidth
          value={newTodoValue.des}
          onChange={(e) =>
            setNewTodoValue({ ...newTodoValue, des: e.target.value })
          }
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose}>الغاء</Button>
        <Button onClick={handleEdit}>تعديل</Button>
      </DialogActions>
    </Dialog>
  );
}
