import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import Divider from "@mui/material/Divider";
import ToggleButton from "./component/ToggleBtn";
import { useEffect, useState } from "react";
// custom Dir RTL
import rtlPlugin from "stylis-plugin-rtl";
import { prefixer } from "stylis";
import { CacheProvider } from "@emotion/react";
import createCache from "@emotion/cache";
import TodoItem from "./component/TodoItem";
import AddTodoInp from "./component/AddTodoInp";
// Other
import { v4 as uuidv4 } from "uuid";
// context
import { useContext } from "react";
import { todoContext } from "./context/context";

// custom Dir RTL
const cacheRtl = createCache({
  key: "muirtl",
  stylisPlugins: [prefixer, rtlPlugin],
});

export default function BasicCard() {
  const { todos, setTodos } = useContext(todoContext);
  const [alignment, setAlignment] = useState("all");
  // useEffect(() => {
  //   const t = JSON.parse(localStorage.getItem("REACT-Todos"));
  //   if (t) setTodos(t);
  // },[]);
  const [addTodoInp, setAddTodoInp] = useState("");

  let reTodos = todos;
  if (alignment === "done") reTodos = todos.filter((t) => t.completed);
  if (alignment === "non-done") reTodos = todos.filter((t) => !t.completed);
  const todoJSX = reTodos.map((t) => {
    return <TodoItem key={t.id} data={t} />;
  });

  function handleAddBtn() {
    const t = [
      ...todos,
      { id: uuidv4(), title: addTodoInp, completed: false, des: "" },
    ];
    setTodos(t);
    // localStorage.setItem("REACT-Todos", JSON.stringify(t));
    setAddTodoInp("");
  }

  return (
    <CacheProvider value={cacheRtl}>
      <Card sx={{ minWidth: 275 }}>
        <CardContent>
          <Typography sx={{ fontWeight: 500 }} variant="h2" component="h1">
            مهامي
          </Typography>
          <Divider />
          <ToggleButton alignment={alignment} setAlignment={setAlignment} />
          <div className="scroll-bar">{todoJSX}</div>

          <AddTodoInp
            handleAddBtn={handleAddBtn}
            handleInpVal={setAddTodoInp}
            inpVal={addTodoInp}
          />
        </CardContent>
      </Card>
    </CacheProvider>
  );
}
