import { IconButton } from "@mui/material";
import Grid from "@mui/material/Grid2";
import Card from "@mui/material/Card";
import Typography from "@mui/material/Typography";
import { useState } from "react";
// context
import { useContext } from "react";
import { todoContext } from "../context/context";
// ICON
import CheckIcon from "@mui/icons-material/Check";
import DeleteIcon from "@mui/icons-material/Delete";
import EditIcon from "@mui/icons-material/Edit";
import ConfigModal from "./ConfigModal";
import EditModal from "./EditModal";

export default function TodoItem({ data }) {
  const [open, setOpen] = useState(false);
  const [openEditModal, setOpenEditModal] = useState(false);

  const { todos, setTodos } = useContext(todoContext);

  function handleCheckClick() {
    const newTodo = todos.map((e) => {
      if (e.id === data.id) {
        e.completed = !e.completed;
      }
      return e;
    });
    // localStorage.setItem("REACT-Todos", JSON.stringify(newTodo));
    setTodos(newTodo);
  }

  return (
    <Card
      sx={{
        minWidth: 275,
        background: data.completed ? "#369cf5" : "#0568bf",
        color: "white",
        marginBottom: "10px",
        padding: "15px 5px",
      }}
    >
      <ConfigModal id={data.id} open={open} setOpen={setOpen} />
      <EditModal data={data} open={openEditModal} setOpen={setOpenEditModal} />
      <Grid container>
        <Grid
          alignContent={"center"}
          size={{ xs: 7, md: 8 }}
          sx={{ padding: "0 10px" }}
        >
          <Typography
            textAlign="left"
            variant="h5"
            component="h1"
            sx={{ textDecoration: data.completed ? "line-through" : "" }}
          >
            {data.title}
          </Typography>
          <Typography textAlign="left" variant="h6" component="h1">
            {data.des}
          </Typography>
        </Grid>
        <Grid size={{ xs: 5, md: 4 }}>
          <IconButton
            onClick={() => {
              handleCheckClick();
            }}
            sx={{ background: "white", color: "#55ff55" }}
          >
            <CheckIcon />
          </IconButton>
          <IconButton
            sx={{
              background: "white",
              color: "#444",
              margin: "0 5px",
            }}
            onClick={() => setOpenEditModal(true)}
          >
            <EditIcon sx={{ md: { fontSize: 40 } }} />
          </IconButton>
          <IconButton
            onClick={() => setOpen(true)}
            sx={{ background: "white", color: "#ff5555" }}
          >
            <DeleteIcon />
          </IconButton>
        </Grid>
      </Grid>
    </Card>
  );
}
