import Grid from "@mui/material/Grid2";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
export default function AddTodoInp({ handleAddBtn, handleInpVal, inpVal }) {
  return (
    <Grid container spacing={2}>
      <Grid size={8}>
        <TextField
          fullWidth
          id="outlined-basic"
          label="مهمة جديدة"
          variant="outlined"
          value={inpVal}
          onChange={(e) => {
            handleInpVal(e.target.value);
          }}
        />
      </Grid>
      <Grid size={4}>
        <Button
          onClick={handleAddBtn}
          fullWidth
          sx={{ height: "100%" }}
          variant="contained"
          disabled={inpVal.length === 0}
        >
          اضافة
        </Button>
      </Grid>
    </Grid>
  );
}
