import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogTitle from "@mui/material/DialogTitle";
// context
import { useContext } from "react";
import { todoContext } from "../context/context";

export default function ConfigModal({ id, open, setOpen }) {
  const { todos, setTodos } = useContext(todoContext);

  const configDelete = () => {
    const newTodo = todos.filter((e) => id != e.id);
    // localStorage.setItem("REACT-Todos", JSON.stringify(newTodo));
    setTodos(newTodo);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">
        هل انت متاكد من حذف المهمة
      </DialogTitle>

      <DialogActions>
        <Button onClick={handleClose}>الغاء</Button>
        <Button onClick={configDelete} autoFocus>
          حذف
        </Button>
      </DialogActions>
    </Dialog>
  );
}
